.preloader {
  height: 100vh;
  width: 100%;
  background: #000;
  color: #fff;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  z-index: 4; 
  overflow: hidden; 
  display: flex; 
  align-items: center;
  justify-content: center;
}

.preloader .texts-container {
  height: 60px;
  width: auto; /* Adjust width based on content */
  font-size: 30px;
  font-weight: 800;
  color: #fff;
  overflow: hidden; 
  display: flex; 
  align-items: center;
  justify-content: center; /* Center the text */
}

.texts-container {
  height: auto; /* Allow the container to adjust its height based on content */
  overflow: visible; /* Ensure content isn't cut off */
  letter-spacing: -0.5px; /* Adjust spacing between characters */
}

.texts-container span {
  font-size: 1.2em; /* Increase font size by 20% */
  display: inline-block;
  margin: 0 5px; /* Add small margin for spacing between words */
}

.texts-container span:nth-child(3) {
  color: red; /* Make the third word red */
}

.landing {
  display: flex;
  flex-direction: column;
  height: auto; /* Adjust to content */
}

.texts-container {
  flex-grow: 1;
}