/* Home page styling */

body {
  line-height: 1.6;
}

.home-container {
  padding-top: 2900px; 
  position: relative;
  padding-bottom: 0px;
}

/* Text container styling */
.text-container {
  z-index: 2; 
  position: absolute;
  display: grid;
  flex-direction: column;
  gap: 100px;
  place-items: center;
  align-content: center;
  min-height: 100vh;
}

/* Lottie animation container */
.animation1-container {
  position: absolute;
  top: 10%;
  left: 35%;
  max-width: 300px;
  z-index: 1;
}

/* Images container styling */
.images {
  cursor:grab;
  display: flex;
  gap: 4vmin;
  position: absolute;
  left: 50%;
  top: 14%;
  transform: translate(-100%, -50%); 
  z-index: 1; 
  transition: opacity 0.75s ease-out, transform 0.75s ease-out;
}

/* Image styling */
.images > .image {
  height: 40vw;
  width: 25vw;  
  max-width: 300px;
  max-height: 450px;
  object-fit: cover;
  object-position: right;
  cursor: grab; 
}

.images.show {
  opacity: 1;
}

/* Cursor when dragging */
.images > .image.grabbing {
  cursor: grabbing;
}

/* Grid container for languages and tools */
.grid-container {
  display: grid;
  grid-template-columns: repeat(4, 1fr); 
  justify-items: center; 
  align-items: center; 
  gap: 10px; 
}
.lang-row2 {
  grid-template-columns: repeat(3, 1fr); 
  justify-self: center; 
  margin-top: -25px;
  margin-left: 100px;
  margin-right: 100px;
}
.tools-grid {
  grid-template-columns: repeat(5, 1fr); 
  gap: 40px; 
}
.ide-grid {
  grid-template-columns: repeat(3, 1fr); 
  justify-self: center; 
  margin-top: -25px;
  margin-left: 50px;
  margin-right: 50px;
}
.grid-item {
  display: flex;
  flex-direction: column; 
  align-items: center;
  text-align: center;
  width: 100px; 
  margin-bottom: 1rem; 
  margin-top: 3rem;
}

/* Style for the circular background and image container */
.image-container {
  width: 130px; 
  height: 130px;
  background-color: white;
  border-radius: 50%; 
  overflow: hidden; 
  display: flex;
  align-items: center;
  justify-content: center;
  margin-bottom: 0.5rem; 
}

/* Style for the images inside the circles */
.image-container img {
  width: 80%; 
  height: auto; 
  object-fit: cover; 
}

/* Style for the text labels */
.gradient-text-windows {
  background: linear-gradient(to right, red, yellow, blue, green);
  background-clip: text;
  -webkit-text-fill-color: transparent;
}
.gradient-text-linux {
  background: linear-gradient(to right, black, white);
  background-clip: text;
  -webkit-text-fill-color: transparent;
}

/* Style for the blobs */
.blob {
  position: absolute;
  width: 100%;
  height: 100%;
  overflow: hidden;
}
.blob svg {
  position: absolute;
  top: -25%;
  left: -50%;
  width: 100%;
  height: 100%;
}
.blob2 {
  position: absolute;
  width: 90%;
  height: 100%;
  overflow: hidden;
}
.blob2 svg {
  position: absolute;
  top: 55%;
  left: 0%;
  width: 100%;
  height: 100%;
}
.blob3 {
  position: absolute;
  width: 100%;
  height: 100%;
  overflow: hidden;
}
.blob3 svg {
  position: absolute;
  top: 20%;
  left: 60%;
  width: 80%;
  height: 70%;
}