
/* Global styles */
html, body {
  margin: 0;
  padding: 0;
  height: 100%;
  overflow-x: hidden; 
  overflow-y: auto;
}

h1 {
  font-family: 'Work Sans', sans-serif; 
  font-weight: 700;
  font-size: 3vw; 
  text-align: center;
  margin-bottom: 1.25vw;
}

h2 {
  font-family: 'Work Sans', sans-serif; 
  font-weight: 700;
  font-size: 1.75vw; 
  text-align: center;
  margin-bottom: 1vw;
}

h3 {
  font-family: 'Work Sans', sans-serif; 
  font-weight: 700;
  font-size: 1.25vw; 
  text-align: center;
  margin-bottom: 0.75vw;
}

.bold-text {
  font-weight: bold;
  color: rgb(244, 63, 112);
}

/* Container styling */
.container {
  width: 100vw;
  min-height: 100vh;
  transition: background-color 0.5s, color 0.5s;
}
/* Customize scrollbar for Webkit browsers */
::-webkit-scrollbar {
  width: 12px; 
}

::-webkit-scrollbar-track {
  background: #f1f1f1; 
}
::-webkit-scrollbar-thumb {
  background: #888; 
  border-radius: 10px; 
}
::-webkit-scrollbar-thumb:hover {
  background: #555; 
}

/* Light mode */
.container-light {
  background: #96000014;
  color: #000; 
}

/* Dark mode */
.container-dark {
  background: #222;
  color: #fff; 
}

.project-info {
  background-color: var(--bg-color-light);
  color: var(--text-color-light);
}

.container-dark .project-info {
  background-color: var(--bg-color-dark);
  color: var(--text-color-dark);
}

.hidden {
  transform: translateX(-100%); 
  opacity: 0;
  filter: blur(10px); 
  transition: transform 1.0s ease-out, opacity 1.0s ease-out, filter 1.0s ease-out;
}

.show {
  transform: translateX(0); 
  opacity: 1;
  filter: blur(0); 
}

.no-scroll {
  overflow-y: hidden;
}

/* hyperlinks styling */
a {
  color: rgb(158, 158, 21);
}
a:hover {
  color: rgb(207, 207, 11);
  transition: 0.35s;
}

/* Style for highlighted text w/cursor */
::selection {
  background-color: #f43f70; 
}
/* For WebKit browsers */
::-webkit-selection {
  background-color: #f43f70; 
}
/* For Mozilla browsers */
::-moz-selection {
  background-color: #f43f70; /* Highlight background color */
}
/* For Internet Explorer and Edge */
::selection {
  background: #f43f70; /* Highlight background color */
}