/* Contact page styling */
.contact-container {
  position: relative;
  padding-top: 300px;
  overflow: hidden;
  padding-bottom: 100px;
  z-index: 0;
}

/* Wrapper to align content side by side */
.content-wrapper {
  display: flex;
  justify-content: space-between;
  align-items: flex-start; 
  gap: 50px;
}

/* Contact information styling */
.contact-info {
  z-index: 1000;
  margin-top: -50px;
  flex: 1;
  font-size: 22px;
}

/* Social links container */
.social-links {
  display: flex;
  flex-direction: column; 
  gap: 21px;
}

/* Circular logo styling */
.linkedin-logo {
  position: absolute;
  width: 100px; 
  height: 100px;
  left: 20%;
  border-radius: 50%;
  object-fit: cover;
}

.github-logo-about {
  position: absolute;
  width: 60px; 
  height: 60px;
  left: 25%;
  border-radius: 100%;
  object-fit: cover;
}

/* Lottie animation container */
.animation2-container {
  flex: 1;
  margin-top: -150px;
}

.blob-contact {
  position: absolute;
  width: 80%;
  height: 80%;
}
.blob-contact svg {
  position: absolute;
  top: -15%;
  left: 40%;
  width: 100%;
  height: 100%;
}
