/* About page styling */
.about-container {
  position: relative;
  padding-top: 2500px; 
  padding-bottom: 100px;
}
.text-container {
  z-index: 2; 
  position: relative;
}

/*image styling for top of page*/
#profile {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 5rem;
  height: 40vh;
  padding: 2vw;
  margin-bottom: 100px;
}
.profile-info {
  display: flex;
  gap: 5rem; 
}
.section__pic-container {
  height: 50vh;
  width: 50vh;
}
.section__pic-container img {
  width: 100%;
  height: auto;
  border-radius: 90%; 
}
.profile-text {
  display: flex;
  flex-direction: column;
  align-items: flex-start; 
  text-align: left; 
}
.title {
  margin: 0.5rem 0;
  font-size: 5.5vh;
}

/* Academic Journey Section Styling */
.academic-journey {
  display: flex;
  align-items: center;
  gap: 2rem; 
  margin: 2rem 0;
}
.academic-image {
  flex: 1;
  margin-right: 2vw;
}
.academic-image img {
  width: 100%;
  height: 80vh; 
  border-radius: 10px; 
  border: 5px solid; 
  border-radius: 15px; 
  object-fit: cover;
}
.academic-text {
  flex: 2; 
  max-width: 600px;
  margin: 0 auto; 
}
.academic-text .about-paragraph {
  margin-bottom: 1rem;
}

/* Professional Experience Section Styling */
.professional-experience {
  display: flex;
  align-items: center;
  gap: 2vh; 
  margin: 2vh 0;
}
.professional-text {
  flex: 2; 
  max-width: 600px;
  margin: 0 auto; 
}
.professional-image {
  flex: 1;
  margin-left: 2vw;
}
.professional-image img {
  width: 100%;
  height: 70vh; 
  border-radius: 10px; 
  border: 5px solid; 
  border-radius: 15px; 
  object-fit: cover;
}
.about-paragraph {
  margin-bottom: 1vh;
}

/* Additional Section Styling */
.additional-section {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  gap: 2rem; 
  margin: 2rem 0;
}
.additional-image {
  flex: 1;
  margin-right: 2vw;
  overflow: hidden; 
  width: 100%;
  max-width: 900px; 
  height: 600px; 
  border-radius: 15px;
  border: 5px solid;
}
.additional-image img {
  width: 100%;
  height: 70vh;
  object-fit: cover; /* Maintains aspect ratio and covers container */
  transform: scale(1.5); /* Zooms in on the image */
  transform-origin: right; /* Focuses zoom on the right side */
}
.additional-text {
  flex: 2; 
  max-width: 600px;
  margin: 0 auto; 
}
.additional-text .about-paragraph {
  margin-bottom: 1rem;
}

/*blob styling*/
.blob-about1 {
  position: absolute;
  width: 100%;
  height: 100%;
  overflow: hidden;
}
.blob-about1 svg {
  position: absolute;
  top: 10%;
  left: 0%;
  width: 100%;
  height: 100%;
}